export default function reducer(state={
	paintings:[],
	collected: false,
	bannerImages: [],
}, action){
	switch (action.type){
		case "FETCH_PAINTINGS": {
			return{...state, fetching:true}
		}
		case "FETCH_PAINTINGS_FUFILLED": {
			return{
				...state,
				paintings: action.payload,
			}
		}
		case "FETCH_BANNER_FUFILLED": {
			return{
				...state,
				bannerImages: action.payload,
			}
		}
	default:{
			return{state}
		}
	}
}


