import React from 'react';
import PropTypes from 'prop-types';
import { Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle, Button, Container, Row, Col } from 'reactstrap';
import posed from 'react-pose';
import {keyframes} from 'popmotion';
import classNames from 'classnames/bind';
import scrollLeft from '../../icons/scrollLeft.svg'
import scrollRight from '../../icons/scrollRight.svg'
import './PaintingObject.scss';



function convertImage(imageData){
  let binary = new Uint8Array(imageData)
  let blob = new Blob([binary])
  let img = new Image()
  img.src = URL.createObjectURL(blob)
	return img.src;
}





export class PaintingObject extends React.Component{
	constructor(props){
		super(props);
		
	
	}

	

		


	


	render(){

	 var  result = ''
	 var painting = this.props.art.data
	if(painting != null && painting.length != 0){
	 	  result = painting.map((piece) => 
	 	  	<Col className='HomePiece' >
		      <Card className="HomeArtWork">
		        <CardImg className="HomeArtWorkImg" src= {convertImage(piece.Painting_Image.data)} alt={piece.PaintingTitle} />
		      </Card>
		    </Col>
			);
	 }
	
		return(

			<Container className='mainDiv' >
			<div className='slider'>
			{result}
			</div>
			</Container>
			
			);
	
} 

}



PaintingObject.defaultProps = {
	art : [],
};



