import React from 'react';
import PropTypes from 'prop-types';
import logo from '../../icons/Logo.svg'
import {NavBar} from '../';
import {SkywardsBanner} from '../MainPage'
import {connect} from "react-redux";
import {fetchBanner} from "../../actions/bannerActions"

@connect((store) => {
	return{
		banner: store.gallery.bannerImages
	};
})

export class HomeDiv extends React.Component{
	constructor(props){
		super(props);
	}

	componentWillMount(){
    this.props.dispatch(fetchBanner())
  }

	render(){
		return(
			
			<div className="section">
			<div className="leftHalf">
			<NavBar/>
			<img className="logo" src={logo}/>
			</div>
			<SkywardsBanner images={this.props.banner}/>
			</div>
		);
	}
} 



