import React from 'react';
import PropTypes from 'prop-types';
import {PaintingObject} from './'
import {ControlledCarousel} from '../';
import {connect} from "react-redux";
import {fetchPaintings} from "../../actions/galleryActions";
import {scrollArea} from 'react-scrollbar-x';


@connect((store) => {
	return{
		paintings: store.gallery.paintings
	};
})



export class GalleryDiv extends React.Component{
	constructor(props){
		super(props);
	}

	galleryContainer = React.createRef();

   
	

	componentWillMount(){
    this.props.dispatch(fetchPaintings())
    
  }

  	




	render(){
		
		return(
			<div className="gallerysection" >
			<h1> Gallery</h1>
			<PaintingObject art={this.props.paintings} self={this.galleryContainer} ref={this.galleryContainer}/>
			
			</div>
		);
	}
} 


