import React from 'react';
import PropTypes from 'prop-types';
import {PaintingGallery} from './'
import {ControlledCarousel} from '../';
import {connect} from "react-redux";
import {fetchPaintings} from "../../actions/galleryActions"



@connect((store) => {
	return{
		paintings: store.gallery.paintings
	};
})


export class GallerySection extends React.Component{
	constructor(props){
		super(props);
	}

	componentWillMount(){
	
    this.props.dispatch(fetchPaintings())
    
  }

  	




	render(){
		
		return(
			<div className="gallerypage">
			<h1> Gallery</h1>
			<PaintingGallery art={this.props.paintings}/>
		
			
			</div>
		);
	}
} 


