import React from 'react';
import {MapContainer} from './GoogleMap';
import './ContactSection.scss'
export class ContactDiv extends React.Component{
	constructor(props){
	super(props)
		this.state ={
			UserName : '',
			UserEmail : '',
			UserMessage : '',
		}
	}
	

	render(){
		return(
			<div id='Contact'className="contact">
			<MapContainer/>
			</div>
		);
	}
} 




