import React from 'react';
import PropTypes from 'prop-types';

import { Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle, Button, Container,Col, Row } from 'reactstrap';
import './PaintingGallery.scss';




function convertImage(imageData){
  let binary = new Uint8Array(imageData)
  let blob = new Blob([binary])
  let img = new Image()
  img.src = URL.createObjectURL(blob)
	return img.src;
}

export class PaintingGallery extends React.Component{
	constructor(props){
		super(props);
	}

	render(){

	 var  result = ''
	 var painting = this.props.art.data
	 
	if(painting != null && painting.length != 0){
		const rowCount = painting.length / 3;
		 console.log(painting.length / 3)
		 
	 	  result =  
	 	  	Array(rowCount).fill().map((_, rowIndex) =>
	 	  		<Row key={rowIndex}>

	 	  		{
	 	  		painting.slice(rowIndex * 3, (rowIndex *3) + 3).map(piece => (

		 	  			<Col className='col-sm-4 piece'>
			      		<Card key className="ArtWork">
			        	<CardImg className="ArtWorkImg" src= {convertImage(piece.Painting_Image.data)} alt={piece.PaintingTitle} />
			      		</Card>
			    		</Col>
				))}		
			</Row>
			)
			
			
	 }
	
		return(
			<Container className='MainDiv container-fluid'>
			{result}
			</Container>
			);
	
} 


}
PaintingGallery.defaultProps = {
	art : [],
};



